body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.signup input,
label,
select {
	margin: 5px 0;
	padding: 5px;
}

.container-signup {
	margin: 150px;
}

.container-signup p {
	margin-left: 150px;
}

input[type=submit] {
	color: black;
	background-color: whitesmoke;
	font-style: italic;

	border-radius: 4px;
	border: 2px solid gray;

	margin: 5px 0;
	padding: 5px;
	height: 25px;
	border-radius: 10px;
}

.container-login {
	margin: 150px;
}

.home-user {
	margin: 100px;
}

.fotoprofile {
	width: 200px;
	height: 200px;
	margin-left: 300px;
	background-color: #ccc;
}

.navbar {
	width: 100%;
	position: fixed;
	top: 0;
	z-index: 1;
	list-style: none;
	text-decoration: none;
}

.navbar ul {
	width: 100%;
	display: -webkit-flex;
	display: flex;
	list-style: none;
	text-decoration: none;
	border: solid orange;
}

.navbar li {
	list-style: none;
	text-decoration: none;
	margin: 0 20px;
}

.printerUser {
	margin: 150px;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.container-signupPro {
	border: solid red;
	margin: 150px;
}

